import React, { useState, useEffect } from 'react';
import { Button } from 'primereact/button';
import { Carousel } from 'primereact/carousel';
import { Tag } from 'primereact/tag';

export default function ProductCarousel() {
    const [products, setProducts] = useState([
        {
            path: '/images/products/Shooter.png',
            name: "Shooter"
        },
        {
            path: '/images/products/Power.png',
            name: "Power"
        },
        {
            path: '/images/products/Plantor.png',
            name: "Plantor"
        },
    ]);
    const responsiveOptions = [
        {
            breakpoint: '1400px',
            numVisible: 2,
            numScroll: 1
        },
        {
            breakpoint: '1199px',
            numVisible: 3,
            numScroll: 1
        },
        {
            breakpoint: '767px',
            numVisible: 2,
            numScroll: 1
        },
        {
            breakpoint: '575px',
            numVisible: 1,
            numScroll: 1
        }
    ];
    

    const productTemplate = (product) => {
        return (
            <div style={{display:'flex', flexDirection:'column', alignItems:'flex-start'}}>
                <h1 style={{color:'#7BD482', textAlign:'center', fontSize:'1.125rem',fontFamily:'Inter', fontStyle:'italic'}}>{product.name}</h1>
                <h6 style={{maxWidth: '70%'}}>
                There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form.
                </h6>
                <img src={product.path} style={{width:'90%', height:'90%'}}/>
            </div>
        );
    };

    return (
        <div className="card">
            <Carousel value={products} numVisible={3} numScroll={3} responsiveOptions={responsiveOptions} className="custom-carousel" circular
            autoplayInterval={3000} itemTemplate={productTemplate} />
        </div>
    )
}
         
